import { useState, useEffect } from 'react';

export function useHeaderLogic() {
   const [menuOpen, setMenuOpen] = useState(false);

   const toggleMenu = () => {
      setMenuOpen(!menuOpen); // Invert the menu state
   };

   const closeMenu = () => {
      setMenuOpen(false); // Close the menu
   };

   // Adugai un asculttor de evenimente pentru a nchide meniul la scroll
   useEffect(() => {
      window.addEventListener('scroll', closeMenu);

      // Cleanup pentru a elimina asculttorul la dezmontare
      return () => {
         window.removeEventListener('scroll', closeMenu);
      };
   }, []);

   // Ascult evenimentul clic pe ntreaga pagin pentru a nchide meniul
   useEffect(() => {
      const handleClickOutside = (event) => {
         if (menuOpen && !event.target.closest('.header')) {
            closeMenu();
         }
      };

      document.addEventListener('click', handleClickOutside);

      // Cleanup pentru a elimina asculttorul la dezmontare
      return () => {
         document.removeEventListener('click', handleClickOutside);
      };
   }, [menuOpen]);

   return {
      menuOpen,
      toggleMenu,
      closeMenu,
   };
}
