import { useParams, Link } from 'react-router-dom';
import Title from '../Utils/Title';
import { SlideshowLightbox } from 'lightbox.js-react';
import 'lightbox.js-react/dist/index.css';
import fetchDataNews from '../../data/dataNews';

function NewsDetails() {
   const { id } = useParams();

   const anunt = fetchDataNews.find((item) => item.id === parseInt(id));

   return (
      <section className="news-details">
         <div className="container">
            <div className="news-details__body">
               <Title class="news-details__title">{anunt.title}</Title>
               <p className="news-details__text">{anunt.description}</p>
               <SlideshowLightbox
                  theme="lightbox"
                  className="news-details__container"
               >
                  {anunt.imgs.map((img, index) => (
                     <img
                        className="news-details__img"
                        key={index}
                        src={img}
                        alt={`Imagine ${index + 1}`}
                        loading="lazy"
                     />
                  ))}
               </SlideshowLightbox>
               <div className="news-details__go-back-wrapper">
               <button onClick={() => window.history.back()} className="news-details__go-back box-sh">
                  <svg
                     className="news-details__icon"
                     xmlns="http://www.w3.org/2000/svg"
                     width="24"
                     height="24"
                     viewBox="0 0 24 24"
                  >
                     <path
                        fill="currentColor"
                        d="M12.29 8.71L9.7 11.3a.996.996 0 0 0 0 1.41l2.59 2.59c.63.63 1.71.18 1.71-.71V9.41c0-.89-1.08-1.33-1.71-.7z"
                     />
                  </svg>
               </button>
               </div>
               
            </div>
         </div>
      </section>
   );
}

export default NewsDetails;
