import React, { useEffect, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import ImportImagesSwiper from '../../data/ImportImagesSwiper';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

// import required modules
import { Autoplay, Pagination, Navigation } from 'swiper/modules';
import Title from '../Utils/Title';

export default function App() {
   const [windowWidth, setWindowWidth] = useState(window.innerWidth);

   useEffect(() => {
      // Ascult schimbrile de lime a ferestrei
      window.addEventListener('resize', handleWindowResize);

      return () => {
         // Elimin asculttorul la dezasamblarea componentei
         window.removeEventListener('resize', handleWindowResize);
      };
   }, []);

   const handleWindowResize = () => {
      setWindowWidth(window.innerWidth);
   };
   return (
      <section className="slider">
         <div className="container">
            <div className="slider__body">
               <Title class="slider__title">
                  Memorii, O Privire n Trecut.
               </Title>
               <Swiper
                  slidesPerView={1}
                  spaceBetween={30}
                  loop={true}
                  pagination={{
                     clickable: true,
                  }}
                  autoplay={{
                     delay: 4000,
                     disableOnInteraction: false,
                  }}
                  navigation={windowWidth > 768}
                  modules={[Autoplay, Pagination, Navigation]}
                  className="mySwiper box-sh"
                  style={{
                     '--swiper-pagination-color': '#ffeeff',
                     '--swiper-pagination-bullet-inactive-color': '#efefef70',
                     '--swiper-pagination-bullet-inactive-opacity': '1',
                     '--swiper-pagination-bullet-size': '10px',
                     '--swiper-pagination-bullet-width': '10%',
                     '--swiper-pagination-bullet-border-radius': '32px',
                     '--swiper-pagination-bullet-horizontal-gap': '10px',
                     // for arrows
                     '--swiper-navigation-size': '30px',
                     '--swiper-theme-color': '#ffeeff',
                  }}
               >
                  {ImportImagesSwiper.map((el, index) => (
                     <SwiperSlide key={index}>
                        <img src={el} alt="" />
                     </SwiperSlide>
                  ))}
               </Swiper>
            </div>
         </div>
      </section>
   );
}
