import DidacticPerson from '../components/Didactic/DidacticPerson';
import dataDidactic from '../data/dataDidactic';
import Title from '../components/Utils/Title';

function Didactic() {
   return (
      <main className="main">
         <section className="didactic">
            <div className="container">
               <div className="didactic__body">
                  <Title class="didactic__title">
                     Cadrul didactic al IPLT "Dragos Voda"
                  </Title>
                  <div className="didactic__grid">
                     {dataDidactic.map((el) => (
                        <DidacticPerson key={el.id} el={el} />
                     ))}
                  </div>
               </div>
            </div>
         </section>
      </main>
   );
}
export default Didactic;
