import React, { useState } from 'react';
import ReactPaginate from 'react-paginate';
import fetchDataNews from '../../data/dataNews';
import NewsLink from './NewsLink';
import Title from '../Utils/Title';

function News() {
   const [currentPage, setCurrentPage] = useState(0); // Indexarea ncepe de la 0
   const itemsPerPage = 9;

   // Simuleaz o list de tiri
   const newsData = fetchDataNews; // Lista ta de tiri

   const pageCount = Math.ceil(newsData.length / itemsPerPage);

   const handlePageClick = (data) => {
      setCurrentPage(data.selected);

      // Adaug o intrare n istoric
      window.history.pushState({ page: data.selected + 1 }, `Page ${data.selected + 1}`, `?page=${data.selected + 1}`);
   };

   const startIndex = currentPage * itemsPerPage;
   const endIndex = startIndex + itemsPerPage;
   const currentNews = newsData.slice(startIndex, endIndex);

   return (
      <section className="news">
         <div className="container">
            <div className="news__body">
               <Title class="news__title">Noutile Institutiei</Title>
               <div className="news__grid">
                  {currentNews.map((el) => (
                     <NewsLink key={el.id} el={el} />
                  ))}
               </div>
            </div>
         </div>
         <ReactPaginate
            previousLabel={
               <svg
               className='paginate-icon'
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
               >
                  <path
                     fill="currentColor"
                     d="M12.29 8.71L9.7 11.3a.996.996 0 0 0 0 1.41l2.59 2.59c.63.63 1.71.18 1.71-.71V9.41c0-.89-1.08-1.33-1.71-.7z"
                  />
               </svg>
            }
            nextLabel={
               <svg
               className='paginate-icon'
                  xmlns="http://www.w3.org/2000/svg"
                  width="24"
                  height="24"
                  viewBox="0 0 24 24"
               >
                  <path
                     fill="currentColor"
                     d="m11.71 15.29l2.59-2.59a.996.996 0 0 0 0-1.41L11.71 8.7c-.63-.62-1.71-.18-1.71.71v5.17c0 .9 1.08 1.34 1.71.71z"
                  />
               </svg>
            }
            breakLabel={'...'}
            breakClassName={'break-me'}
            pageCount={pageCount}
            marginPagesDisplayed={2}
            pageRangeDisplayed={5}
            onPageChange={handlePageClick}
            containerClassName={'pagination'}
            subContainerClassName={'pages pagination'}
            activeClassName={'active'}
         />
      </section>
   );
}

export default News;
