import React from 'react';
import { useHeaderLogic } from './headerLogic';
import MenuItem from './MenuItem';
import DarkModeToggle from './DarkModeToggle';

function Header() {
   const { menuOpen, toggleMenu } = useHeaderLogic();

   return (
      <header className={`header ${menuOpen ? 'menu-open' : ''}`}>
         <div className="container">
            <div className="header__body">
               <div className="header__top">
                  <div className="header__logo">
                     <p>IPLT "Dragos Voda"</p>
                  </div>
                  <nav className="menu">
                     <ul className="menu__list">
                        <MenuItem path="/">Acasa</MenuItem>
                        <MenuItem path="/news">Noutati</MenuItem>
                        <MenuItem path="/galerie">Galerie</MenuItem>
                        <MenuItem path="/scurt-istoric/">
                           Scurt Istoric
                        </MenuItem>
                        <MenuItem path="/transparenta">Trasparenta</MenuItem>
                     </ul>
                     <div className="header__burger ">
                        <button
                           type="button"
                           className="header__icon-burger icon-menu"
                           onClick={toggleMenu} // Toggle menu panel
                        >
                           <svg
                              className="header__icon"
                              xmlns="http://www.w3.org/2000/svg"
                              width="24"
                              height="24"
                              viewBox="0 0 24 24"
                           >
                              <g fill="none">
                                 <path d="M24 0v24H0V0h24ZM12.593 23.258l-.011.002l-.071.035l-.02.004l-.014-.004l-.071-.035c-.01-.004-.019-.001-.024.005l-.004.01l-.017.428l.005.02l.01.013l.104.074l.015.004l.012-.004l.104-.074l.012-.016l.004-.017l-.017-.427c-.002-.01-.009-.017-.017-.018Zm.265-.113l-.013.002l-.185.093l-.01.01l-.003.011l.018.43l.005.012l.008.007l.201.093c.012.004.023 0 .029-.008l.004-.014l-.034-.614c-.003-.012-.01-.02-.02-.022Zm-.715.002a.023.023 0 0 0-.027.006l-.006.014l-.034.614c0 .012.007.02.017.024l.015-.002l.201-.093l.01-.008l.004-.011l.017-.43l-.003-.012l-.01-.01l-.184-.092Z" />
                                 <path
                                    fill="currentColor"
                                    d="M20 17.5a1.5 1.5 0 0 1 .144 2.993L20 20.5H4a1.5 1.5 0 0 1-.144-2.993L4 17.5h16Zm0-7a1.5 1.5 0 0 1 0 3H4a1.5 1.5 0 0 1 0-3h16Zm0-7a1.5 1.5 0 0 1 0 3H4a1.5 1.5 0 1 1 0-3h16Z"
                                 />
                              </g>
                           </svg>
                           <svg
                              className="header__icon"
                              xmlns="http://www.w3.org/2000/svg"
                              width="24"
                              height="24"
                              viewBox="0 0 1024 1024"
                           >
                              <path
                                 fill="currentColor"
                                 d="M195.2 195.2a64 64 0 0 1 90.496 0L512 421.504L738.304 195.2a64 64 0 0 1 90.496 90.496L602.496 512L828.8 738.304a64 64 0 0 1-90.496 90.496L512 602.496L285.696 828.8a64 64 0 0 1-90.496-90.496L421.504 512L195.2 285.696a64 64 0 0 1 0-90.496z"
                              />
                           </svg>
                        </button>
                     </div>
                     <DarkModeToggle />
                  </nav>
               </div>
            </div>
         </div>
      </header>
   );
}

export default Header;
