function DidacticItem({ el }) {
   const itemStyle = {
      '--grad-color': el.gradColor, // Set a custom CSS variable
   };
   const currentYear = new Date().getFullYear();
   return (
      <div className="didactic-pers box-sh" style={itemStyle}>
         <div className="didactic-pers__img">
            <img src={el.img} alt={el.name} />
            <span className="didactic-pers__grad"></span>
         </div>
         <div className="didactic-pers__info">
            <p className="didactic-pers__name">{el.name}</p>
            <div className="didactic-pers__exp">
               {`${currentYear - el.exp} Ani Stagiere`}
            </div>
         </div>
      </div>
   );
}
export default DidacticItem;
