// Importai funcia require.context
const importAll = (r) => r.keys().map(r);
// Utilizai require.context pentru a importa toate imaginile din director
const images = importAll(
   require.context(
      '../assets/img/didactic',
      false,
      /\.(jpg|jpeg|png|svg|webp)$/
   )
);
// Exportai matricea cu imagini
export default images;
