import React from 'react';
import { Link } from 'react-router-dom';
import ReadMore from '../Utils/ReadMore';

function NewsLink({ el }) {
  
   return (
      <Link to={`/news/${el.id}`} className="news-item">
         <div className="news-item__img">
            <img src={el.img} alt={el.title} loading="lazy" />
         </div>
         <div className="news-item__info">
            <div className="news-item__info-top">
               <h3 className="news-item__title">{el.title}</h3>
               <ReadMore
                  className="news-item__descript"
                  text={el.description}
               />
            </div>
            <p className="news-item__date">Data publicrii: {el.date}</p>
            <svg
               width="14"
               height="14"
               viewBox="0 0 14 14"
               fill="none"
               xmlns="http://www.w3.org/2000/svg"
               className="news-item__icon"
            >
               <path
                  d="M3.24283 14.0002C0.570117 14.0002 -0.768384 10.7688 1.12151 8.87887L8.87887 1.12151C10.7688 -0.768386 14.0002 0.57012 14.0002 3.24283V10.0002C14.0002 12.657 12.657 14.0002 10.0002 14.0002H3.24283Z"
                  fill="currentColor"
               />
            </svg>
         </div>
      </Link>
   );
}

export default NewsLink;
