import dataDidactic from '../../data/dataDidactic';
import dataGradColor from '../../data/dataGradColor';

function DidacticItem(props) {
   function countPersonsByGrade(data, grade) {
      return data.reduce(
         (count, person) => (person.grad === grade ? count + 1 : count),
         0
      );
   }
   const gradeCount = countPersonsByGrade(dataDidactic, props.grad);
   const gradColor = dataGradColor[props.grad]; // Atribuii culoarea corespunztoare gradului din dataGradColor.

   const itemStyle = {
      '--grad-color': gradColor, // Set a custom CSS variable
   };
   return (
      <div className="didactic-item box-sh" style={itemStyle}>
         <p className="didactic-item__grad">Gradul {props.children}</p>
         <p className="didactic-item__cadrs">{`${gradeCount} ${
            gradeCount === 1 ? 'cadru' : 'cadre'
         }`}</p>
      </div>
   );
}
export default DidacticItem;
